import Layout from '../components/Layout';
import styled from 'styled-components';
import ChatRoomCard from '../components/ChatRoomCard';
import { useQuery } from 'react-query';
import { getChatRoomList } from '../request';
import { useNavigate } from 'react-router-dom';

const mockData = [
  {
    attn_id: '1',
    name: ' ',
    bookName: ' ',
    bookURL: '/homebrary-logo.png',
  },
  {
    attn_id: '2',
    name: ' ',
    bookName: ' ',
    bookURL: '/homebrary-logo.png',
  },
  {
    attn_id: '3',
    name: ' ',
    bookName: ' ',
    bookURL: '/homebrary-logo.png',
  },
];

const Chat = () => {
  const navigate = useNavigate();

  const { isLoading, isSuccess, data } = useQuery(
    ['chatRoom'],
    async () => await getChatRoomList(),
  );

  if (isLoading) {
    return <div>...</div>;
  }

  // const data = mockData;

  return (
    <Layout title={' '}>
      <Container className={'container'}>
        {isSuccess &&
          data?.map((room: any) => (
            <ChatRoomCard
              key={room.attn_id}
              title={room.bookName}
              subTitle={room.name}
              imageURL={room?.bookURL ?? '/homebrary-logo.png'}
              user_id={room?.attn_id}
              rankerInfoClickHandler={() =>
                navigate(
                  `/chats/attn_id=${room.attn_id}&book_id=${room.book_id}&name=${room.name}&bookName=${room.bookName}`,
                )
              }
            />
          ))}
      </Container>
    </Layout>
  );
};

export default Chat;

const Container = styled.div``;
